.wrap-4{margin-top: 100px;}
.wrap-4 .skills h2{font-size: 50px; font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', 
    Arial, sans-serif; color: #fff; font-weight: 550; margin-bottom: 30px; text-transform: capitalize;}
    .wrap-4 .skills .progress{width: 300px; position: relative; border: none; background: rgb(213, 209, 209); border-radius: 10px; height: 7px;}
    .wrap-4 .skills .percent{display: flex; justify-content: space-between; width: 300px;}
    .wrap-4 .skills .progress .progress-report{position: absolute; width: 90%; border-radius: 10px; border: none; height: inherit; background: #f00;}
    .wrap-4 .skills .progress .html{background: rgb(4, 120, 4); width: 0%; animation: html 2.5s linear forwards;}
    .wrap-4 .skills .progress .css{background: rgb(66, 2, 149); width: 0%; animation: css 2.5s linear forwards;}
    .wrap-4 .skills .progress .bootstrap{background: rgb(134, 2, 2); width: 0%; animation: bootstrap 2.5s linear forwards;}
    .wrap-4 .skills .progress .psd{background: rgb(240, 236, 0); width: 0%; animation: psd 2.5s linear forwards;}
    .wrap-4 .skills .progress .sass{background: rgb(233, 2, 2); width: 0%; animation: sass 2.5s linear forwards;}
    .wrap-4 .skills .progress .js{background: rgb(63, 201, 4); width: 0%; animation: js 2.5s linear forwards;}
    .wrap-4 .skills .progress .jquery{background: rgb(0, 120, 122); width: 0%; animation: jquery 2.5s linear forwards;}
    .wrap-4 .skills .progress .react{background: rgb(235, 143, 14); width: 0%; animation: react 2.5s linear forwards;}
    
    .wrap-4 .skills .second{row-gap: 20px; margin-top: 50px; padding: 0 15px;}
    .wrap-4 .skills .second .col-6 .skill-title{text-transform: uppercase; font-size: 14px; margin-bottom: 7px;}
    .wrap-4 .education .edu-head{font-size: 50px; font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', 
        Arial, sans-serif; color: #fff; font-weight: 550; margin-bottom: 30px; text-transform: capitalize;}
      .wrap-4 .education .hr{width: 50px; height: 1px; border: 1px solid rgb(188, 186, 186); background: rgb(188, 186, 186);}
      .wrap-4 .education h3{font-weight: lighter; color: #fff; font-family: sans-serif; text-transform: capitalize; font-size: 17px; font-weight: 550; line-height: 40px; }
      .wrap-4 .education .edu-details{margin-bottom: 50px;}
      .wrap-4 .education .edu-details p{font-family: sans-serif; text-transform: capitalize; font-size: 18px; font-weight: 520; margin-top: 25px; color: #7188d7; line-height: 25px;}
      .wrap-4 .education .edu-details span{text-transform: capitalize; font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; padding: 0 2px;}
      .wrap-4 .education .row{padding-left: 15px; padding-top: 5px;}

      @keyframes html {
        100%{width: 95%;}
      }
      
      @keyframes css {
        100%{width: 95%;}
      }
      
      @keyframes bootstrap {
        100%{width: 90%;}
      }
      
      @keyframes psd {
        100%{width: 95%;}
      }
      
      @keyframes sass {
        100%{width: 90%;}
      }
      
      @keyframes js {
        100%{width: 90%;}
      }
      
      @keyframes jquery {
        100%{width: 85%;}
      }
      
      @keyframes react {
        100%{width: 90%;}
      }

      @media screen and (max-width: 1220px){
        .wrap-4 .row .col-7{flex-basis: 100%;}
        .wrap-4 .row .col-5{flex-basis: 100%; margin-top: 80px;}
        .wrap-4 .skills h2 {font-size: 40px;}
        .wrap-4 .education .edu-head {font-size: 40px;}
    } 
    @media screen and (max-width:980px){
      .wrap-4 .col-7{flex-basis: 100%; max-width: 100%;}
      .wrap-4 .col-5{flex-basis: 100%; max-width: 100%;}
    }
    @media screen and (max-width:767px){
      .wrap-4 .col-7{flex-basis: 100%; max-width: 100%;}
      .wrap-4 .col-5{flex-basis: 100%; max-width: 100%;}
      .wrap-4{margin-top: 40px;}
      .wrap-4 .skills p{font-size: 14px;}
    }
    @media screen and (max-width:460px){
      .wrap-4{margin-top: 40px;}
      .wrap-4 .skills .second .col-6{flex-basis: 100%; max-width: 100%;}
      .wrap-4 .education h3 {font-size: 12px;}
      .wrap-4 .education .edu-details p {font-size: 12px;}
      .wrap-4 .education .edu-details span {font-size: 13px;}
    }