/* .wrap-1{position: fixed; background: #000; top: 0; left: 0; right: 0; width: 100%;} */
.wrap-1 .col-4 .logo h1{font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
     line-height: 80px; font-weight: 400; font-size: 40px; letter-spacing: 5px; color: #f00;}
.menu-link nav ul{list-style: none; display: flex; text-align: center; line-height: 80px; justify-content: flex-end;}
.wrap-1 nav ul li{margin-left: 10px;}
.wrap-1 .col-8 nav ul li a{color: #fff; font-size: 18px; letter-spacing: 2px; padding: 5px 15px;}
.mob{ width: 90px; height: 35px; border: 2px solid; color: #ca3c3c; background: transparent; border-radius: 10px; font: 16px sans-serif; display: none;}
.mob:active{background: #969696;}
.mob:hover{background: #969696;}
.mob span{ display: block; width: 50px; height: 2px; background: #ca3c3c; margin: 5px auto; transition: 0.5s; }
.mobile-flex{position: absolute; top: 10%; right: 0; left: 0; background: #000; z-index: 100;}
.mobile-flex nav ul{list-style: none; display: flex; flex-flow: column wrap; text-align: left; line-height: 50px; justify-content: space-around;}
.mobile-flex nav ul li a{color: #fff; font-size: 16px; letter-spacing: 2px; padding: 10px 15px;}
@media screen and (max-width: 1220px){
    .wrap-1 .col-4 .logo h1{font-size: 30px;}
    .wrap-1 .col-4{flex-basis: 25%;}
    .wrap-1 .col-8{flex-basis: 75%;}
    .wrap-1 .col-8 nav ul{justify-content: flex-start;}
    .wrap-1 .col-8 nav ul li{margin-right: 10px;}
    .wrap-1 .col-8 nav ul li a{padding: 5px 15px; font-size: 16px;}
} 
@media screen and (max-width:980px){
    
    .wrap-1 .col-8 nav ul{justify-content: flex-end;}
    .wrap-1 .col-8 nav ul li{margin-right: 0px;}
    .wrap-1 nav ul li {margin-left: 9px;}
    .wrap-1 .col-8 nav ul li a{padding: 5px 10px; font-size: 14px;}
}
@media screen and (max-width:767px){
    .wrap-1 .col-4{flex-basis: 16.66%; max-width: 16.66%;}
    .wrap-1 .col-8{flex-basis: 83.33%; max-width: 83.33%; text-align: right;}
    .wrap-1 .col-4 .logo h1{font-size: 22px;}
    .wrap-1 .container .row{justify-content: space-between; text-align: center; align-items: center;}
    .menu-link{display: none;}
    .mob{display: inline-block;}
}
@media screen and (max-width:460px){
    .wrap-1 .container .row{justify-content: space-between; text-align: center; align-items: center;}
    .wrap-1 .col-4 .logo h1{font-size: 18px;}
    .wrap-1 .col-4{flex-basis: 33.33%; max-width: 33.33%;}
    .wrap-1 .col-8{flex-basis: 66.66%; max-width: 66.66%; text-align: right;}

}