.wrap-2{position: relative;}
/* .wrap-2 .color{position: absolute; right: 0; max-width: 30%; height: auto;} */
/* .wrap-2 .splash{position: absolute; left: 0; height: auto; width: 30%; height: auto;} */
.wrap-2 .col-6 .title{position: relative; top: 40%;}
.wrap-2 .col-6 .dp{margin-top: 20px;}
.wrap-2 .col-6 .dp img{margin-top: 50px;}
.wrap-2 .col-6 .title p{font-size: 34px; letter-spacing: 3px;}
.wrap-2 .col-6 .title h2{font-size: 50px; line-height: 80px; font-weight: 500;}
.wrap-2 .col-6 .title .front-end{font-size: 40px; font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; display: block; line-height: 60px; margin-bottom: 10px;}
.wrap-2 .col-6 .title .front-end span{color: rgb(255, 0, 0);}
.wrap-2 .col-6 .title .cv{font-size: 17px; padding: 15px 20px; text-transform: capitalize;
    margin-top: 20px; border: 2px; background: rgb(55, 4, 142); font-family: trebuchet MS; color: rgb(209, 208, 208); border-radius: 7px; position: relative; top: 30px;}


    @media screen and (max-width: 1220px){
        .wrap-2 .col-6 .title h2 {font-size: 36px;}
        .wrap-2 .col-6 .title .front-end {font-size: 34px;}
    } 
    @media screen and (max-width:980px){
        .wrap-2 .col-6 .title h2 {font-size: 32px; line-height: 60px;}
        .wrap-2 .col-6 .title .front-end {font-size: 30px;}
    }
    @media screen and (max-width:767px){
        .wrap-2 .col-6 .title h2 {font-size: 30px; line-height: 50px;}
        .wrap-2 .col-6 .title .front-end {font-size: 25px; line-height: 50px;}
    }
    @media screen and (max-width:460px){
        .wrap-2 .col-6{flex-basis: 100%; max-width: 100%;}
        .wrap-2 .col-6:first-child{order: 2;}
        .wrap-2 .col-6 .dp img {margin-top: 0px;}
        .wrap-2 .col-6 .title {top: 5%;}
    }
    