.wrap-3 {margin-top: 100px; padding: 50px 0}
.wrap-3 .content-2{padding-left: 30px;}
.wrap-3 .wallpapers img{height: auto;}
/* .wrap-3 .col-6 .paragraph{padding: 0 20px;} */
.wrap-3 .col-6 .paragraph h2{font-size: 50px; font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', 
    Arial, sans-serif; color: #fff; font-weight: 550; margin-bottom: 30px;}
    .wrap-3 .col-6 .content{margin-top: 50px;}
    .wrap-3 .col-6 .qualities{margin-top: 20px;}
    .wrap-3 .col-6 .qualities  p{line-height: 40px; text-transform: capitalize;}
    .wrap-3 .col-6 .qualities p i{color: rgb(12, 136, 12);}
    .wrap-3 .credits{display: flex; margin-top: 50px; padding: 0 20px;}
    .wrap-3 .credits h3{font-size: 30px; color: #fff; font-weight: 440;}
    .wrap-3 .credits .number{font-size: 30px; color: #7188d7; font-weight: 900;}
    .wrap-3 .credits .prodetails{color: #fff; font-family: trebuchet MS;}
    .wrap-3 .credits .vl{border: 1px solid rgb(159, 158, 158); background: rgb(159, 158, 158); height: 1px; width: 40px; position: relative; top: 18px; transform: rotate(90deg);}
    .wrap-3 .pro-details{padding: 0 20px; margin-top: 10px;}
    .wrap-3 .pro-details p{font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 
        'Lucida Grande', 'Lucida Sans', Arial, sans-serif; color: fff;}
    .wrap-3 .col-6 .know-more{width: 180px; height: 60px; border-radius: 10px; border: none;
         background: rgb(55, 4, 142); color: #fff; font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; text-transform: capitalize; font-size: 16px; color: rgb(209, 208, 208); margin-top: 30px;}
    /* .wrap-3 .col-6 .brief{padding: 0 20px;} */
    .wrap-3 .col-6 .brief p{margin-top: 20px;}


    @media screen and (max-width: 1220px){
        .wrap-3 .col-6 .paragraph h2 {font-size: 40px;}
        .wrap-3 .col-6 .content{margin-top: 50px;}
        .wrap-3 .col-6 .qualities p {line-height: 36px;}
        .wrap-3 .col-6 .brief p {margin-top: 11px;}
        .wrap-3 .col-6 .qualities {margin-top: 11px;}
        .wrap-3 .col-6 .know-more {width: 149px; height: 50px; margin-top: 19px;}
        .wrap-3 .col-6 .content { margin-top: 28px;}
    } 

    @media screen and (max-width:980px){
        .wrap-3 .col-6 .paragraph h2 {font-size: 34px;}
        .wrap-3 .col-6 .content{margin-top: 30px;}
        .wrap-3 .col-6 .qualities p {line-height: 34px;}
        .wrap-3 .col-6 .brief p {margin-top: 9px;}
        .wrap-3 .col-6 .qualities {margin-top: 11px;}
        .wrap-3 .col-6 .know-more {width: 129px; height: 40px; margin-top: 15px;}
        .wrap-3 .col-6 .content { margin-top: 0;}
        .wrap-3 .credits h3{font-size: 26px; color: #fff; font-weight: 440;}
        .wrap-3 .credits .number{font-size: 26px;}
        
    }
    
    @media screen and (max-width:767px){
        .wrap-3{margin-top: 40px;}
        .wrap-3 .col-6 .paragraph h2 {font-size: 30px;}
        .wrap-3 .col-6 .content{margin-top: 15px;}
        .wrap-3 .col-6 .content p{font-size : 14px;}
        .wrap-3 .col-6 .qualities p {line-height: 30px;}
        .wrap-3 .col-6 .brief p {margin-top: 9px;}
        .wrap-3 .col-6 .qualities {margin-top: 11px;}
        .wrap-3 .col-6 .know-more {width: 129px; height: 40px; margin-top: 15px;}
        .wrap-3 .col-6 .content { margin-top: 0;}
        .wrap-3 .credits h3{font-size: 17px; }
        .wrap-3 .credits .number{font-size: 17px;}
        .wrap-3 .pro-details p{font-size: 14px;}
        .wrap-3 .credits .vl {width: 28px; top: 12px;}
    }
    @media screen and (max-width:460px){
        .wrap-3{margin-top: 40px;}
       .wrap-3 .col-6{flex-basis: 100%; max-width: 100%;}
       .wrap-3 .container .row{row-gap: 30px;}
       .wrap-3 .credits h3 {font-size: 24px;}
       .wrap-3 .credits .number{font-size: 24px;}
       .wrap-3 .credits .vl {width: 33px; top: 15px;}
       .wrap-3 .content-2 {padding-left: 0px;}
       .wrap-3 .col-6 .know-more {width: 150px; height: 50px;}
    }
    
    
