.wrap-6{margin-top: 100px;}
.wrap-6 .col-5 .address address h3{text-transform: capitalize; font-size: 16px; font-weight: lighter;
                            color: #909090; font-style: none;}
.wrap-6 .col-5 .address .address-text{font-size: 30px; line-height: 80px; text-transform: capitalize; font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; font-weight: lighter;}
.wrap-6 .col-5 .address address p{font-size: 25px; font-weight: bolder; line-height: 50px;}
.wrap-6 .col-5 .address address .add{border-top: none; border-left: none; margin-bottom: 20px; border-right: none; border-bottom: 1px solid #afaeae;}
.wrap-6 .col-5 .address address .icons{display: flex; margin-top: 20px;}
/* .wrap-6 .col-5 .address address .fa-icon{width: 45px; margin-right: 15px; border-radius: 7px; align-items: center; height: 45px; background: #000094;} */
.wrap-6 .col-5 .address address a{ margin-right: 15px; position: relative; border-radius: 7px; color: #00f;}
.wrap-6 .col-5 .address address a i{color: rgb(255, 255, 255); font-size: 36px;}
.wrap-6 .col-7 .contact-form{padding:  0 20px; padding-left: 40px;}
.wrap-6 .col-7 .contact-form h3{text-transform: capitalize; font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
     font-size: 30px; font-weight: lighter; line-height: 80px; display: inline-block;}
.wrap-6 .col-7 .contact-form .input-name{width: 300px; border: 2px solid #fff; background: transparent; color: #fff; padding: 15px; border-radius: 10px;}
.wrap-6 .col-7 .contact-form .input-email{width: 300px; margin-left: 10px; padding: 15px; border-radius: 10px; border: 2px solid #fff; background: transparent; color: #fff;}
.wrap-6 .col-7 .contact-form .input-subject{width: 620px; padding: 15px; margin-top: 20px; border-radius: 10px;  border: 2px solid #fff; background: transparent; color: #fff;}
/* .wrap-6 .col-7 .contact-form form table tbody tr{border: 2px solid #000;} */
.wrap-6 .col-7 .contact-form textarea{width: 620px; height: 25px; border-radius: 10px; margin-top: 20px;  border: 2px solid #fff; background: transparent; color: #fff;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; padding: 55px 15px;}
.wrap-6 .col-7 .contact-form .btn-send{margin-top: 20px; width: 190px; height: 70px; border-radius: 10px; border: none; background: rgb(55, 4, 142); color: rgb(209, 208, 208);}
.wrap-6 .col-7 .contact-form input, textarea:focus{outline: none;}
.wrap-6 .col-12{margin-top: 100px;}
.wrap-6 .map iframe{border: none; width: 1200px; height: 600px;}
.wrap-7{padding: 20px 0; width: 100%; background: #000; color: #fff; text-align: center;}
.wrap-7 p{ font-family: sans-serif; color: #989898;}

@media screen and (max-width: 1220px){
    .wrap-6 .col-5{flex-basis: 33.33%;}
    .wrap-6 .col-5 .address address p {font-size: 20px;}
    .wrap-6 .col-7 .contact-form{padding:  0 10px; padding-left: 80px;}
    .wrap-6 .col-7{flex-basis: 66.66%;}
    .wrap-6 .col-7 .contact-form .input-name {width: 250px; padding: 18px; }
    .wrap-6 .col-7 .contact-form .input-email{width: 250px; padding: 18px;}
    .wrap-6 .col-7 .contact-form .input-subject { width: 520px; padding: 18px;}
    .wrap-6 .col-7 .contact-form textarea {width: 520px; padding: 36px 15px;}
    .wrap-6 .col-5 .address address p {line-height: 36px;}
    .wrap-6 .col-7 .contact-form .btn-send {width: 149px; height: 50px;}
    .wrap-6 .map{max-width: 900px; margin: auto;}
    .wrap-6 .map iframe{border: none; width: 900px; height: 500px;}
} 
@media screen and (max-width:980px){
    .wrap-6 .col-5{flex-basis: 33.33%; max-width: 33.33%;}
    .wrap-6 .col-5 .address address p {font-size: 14px;}
    .wrap-6 .col-5 .address address p {line-height: 20px;}
    .wrap-6 .col-7 .contact-form{padding:  0 10px; padding-left: 50px;}
    .wrap-6 .col-7{flex-basis: 66.66%; max-width: 66.66%;}
    .wrap-6 .col-7 .contact-form .input-name {width: 200px; padding: 12px; }
    .wrap-6 .col-7 .contact-form .input-email{width: 200px; padding: 12px;}
    .wrap-6 .col-7 .contact-form .input-subject { width: 420px; padding: 12px;}
    .wrap-6 .col-7 .contact-form textarea {width: 420px; padding: 25px 15px;}
    .wrap-6 .map{max-width: 600px; margin: auto;}
    .wrap-6 .map iframe{border: none; width: 600px; height: 400px;}
    .wrap-7{padding: 8px 0; width: 100%; background: #000; color: #fff; text-align: center;}
}

@media screen and (max-width:767px){
     .wrap-6 {margin-top: 40px;}
    .wrap-6 .col-5 .address address p {font-size: 13px;}
    .wrap-6 .col-7 .contact-form .input-name {width: 170px; padding: 12px; }
    .wrap-6 .col-7 .contact-form .input-email{width: 170px; padding: 12px;}
    .wrap-6 .col-7 .contact-form .input-subject { width: 360px; padding: 12px;}
    .wrap-6 .col-7 .contact-form textarea {width: 360px; padding: 25px 15px;}
    .wrap-6 .map{max-width: 600px; margin: auto;}
    .wrap-6 .map iframe{border: none; width: 600px; height: 400px;}
    .wrap-7{padding: 8px 0; width: 100%; background: #000; color: #fff; text-align: center;}
}
@media screen and (max-width:460px){
    .wrap-6 .col-p-12{flex-basis: 100%; max-width: 100%;}
    .wrap-6 .col-p-12{flex-basis: 100%; max-width: 100%;}
    .wrap-6 .col-5 .address address p {font-size: 21px;}
    .wrap-6 .col-7 .contact-form .input-name {width: 152px; padding: 12px; margin-right: 0; }
    .wrap-6 .col-7 .contact-form .input-email{width: 152px; padding: 12px; margin: 0;}
    .wrap-6 .col-7 .contact-form .input-subject { width: 315px; padding: 12px;}
    .wrap-6 .col-7 .contact-form textarea {width: 315px; padding: 25px 15px;}
    .wrap-6 .map{max-width: 300px; margin: auto;}
    .wrap-6 .map iframe{border: none; width: 300px; height: 200px;}
    .wrap-7{padding: 8px 0; width: 100%; background: #000; color: #fff; text-align: center;}
    .wrap-6 {margin-top: 40px;}
    .wrap-6 .col-12{margin-top: 50px;}
    .wrap-6 .col-7 .contact-form{padding:  0 10px; padding-left: 0px;}
    .wrap-6 .col-5 .address address p {line-height: 31px;}
    .wrap-6 .col-5 .address address a i{color: rgb(255, 255, 255); font-size: 30px;}
}