.wrap-5{margin-top: 100px;}
.wrap-5 .container .row{row-gap: 80px;}
.wrap-5 .project-head{font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                      font-size: 50px; line-height: 60px; text-transform: capitalize; font-weight: lighter;}
.wrap-5 .col-6 .projects{max-width: 450px; margin: auto; display: flex;}
.wrap-5 .col-6 .projects .fa{border: none; margin-right: 20px; margin-top: 15px;}
.wrap-5 .col-6 .projects .fa i{color: #93a1f1;}
.wrap-5 .col-6 .projects .project-detail{width: 400px;}
.wrap-5 .col-6 .projects .project-detail h3{font-size: 24px; text-transform: capitalize; line-height: 40px; font-weight: 450; font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; color: #fff; line-height: 50px;}
.wrap-5 .col-6 .projects .project-detail p{margin-bottom: 15px; font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;}
.wrap-5 .col-6 .projects .project-detail a{text-transform: capitalize; color: #9393ff; font-size: 18px; font-weight: 550; text-decoration: underline;}

@media screen and (max-width: 1220px){
    .wrap-5 .project-head {font-size: 40px; line-height: 50px;}
    .wrap-5 .container .row {row-gap: 67px;}
} 
@media screen and (max-width:980px){
    .wrap-5 .col-6 .projects{max-width: 350px; margin: auto; display: flex;}
    .wrap-5 .col-6 .projects .project-detail{width: 320px;}
}
@media screen and (max-width:767px){
    .wrap-5 {margin-top: 40px;}
    .wrap-5 .col-6 .projects .fa i{font-size: 30px;}
    .wrap-5 .col-6 .projects{max-width: 300px; margin: auto; display: flex;}
    .wrap-5 .col-6 .projects .project-detail{width: 280px;}
    .wrap-5 .col-6 .projects .project-detail p{font-size: 13px;}
}
@media screen and (max-width:460px){
    .wrap-5 {margin-top: 40px;}
    .wrap-5 .col-6{flex-basis: 100%; max-width: 100%;}
}